import { formatDistanceToNowStrict } from 'date-fns';

export const formatTime = (date: Date): string => {
    const time: string[] = formatDistanceToNowStrict(date).split(' ');
    let dateTime = '';
    if (time[1] === 'seconds' || time[1] === 'second') {
        if (time[0] === '1') {
            dateTime = ' ';
        }
        if (Number(time[0]) <= 4 && time[0] !== '1') {
            dateTime = ' ';
        }
        if (Number(time[0]) > 4) {
            dateTime = ' ';
        }
    }
    if (time[1] === 'minutes' || time[1] === 'minute') {
        if (time[0] === '1') {
            dateTime = ' ';
        }
        if (Number(time[0]) <= 4 && time[0] !== '1') {
            dateTime = ' ';
        }
        if (Number(time[0]) > 4) {
            dateTime = ' ';
        }
    }
    if (time[1] === 'hours' || time[1] === 'hour') {
        if (time[0] === '1') {
            dateTime = ' ';
        }
        if (Number(time[0]) <= 4 && time[0] !== '1') {
            dateTime = ' ';
        }
        if (Number(time[0]) > 4) {
            dateTime = ' ';
        }
    }
    if (time[1] === 'days' || time[1] === 'day') {
        if (time[0] === '1') {
            dateTime = ' ';
        }
        if (Number(time[0]) <= 4 && time[0] !== '1') {
            dateTime = ' ';
        }
        if (Number(time[0]) > 4) {
            dateTime = ' ';
        }
    }
    if (time[1] === 'months' || time[1] === 'month') {
        if (time[0] === '1') {
            dateTime = ' ';
        }
        if (Number(time[0]) <= 4) {
            dateTime = ' ';
        }
        if (Number(time[0]) > 4) {
            dateTime = ' ';
        }
    }
    if (time[1] === 'years' || time[1] === 'year') {
        if (time[0] === '1') {
            dateTime = ' ';
        }
        if (Number(time[0]) <= 4 && time[0] !== '1') {
            dateTime = ' ';
        }
        if (Number(time[0]) > 4) {
            dateTime = ' ';
        }
    }
    return time[0] + ' ' + dateTime;
};

export const funcDayAndMonth = (date: Date) => {
    const monthDate = date.getMonth();
    const dayDate = date.getDate();
    let monthStr = '';
    switch (monthDate) {
        case 0:
            monthStr = '';
            break;
        case 1:
            monthStr = '';
            break;
        case 2:
            monthStr = '';
            break;
        case 3:
            monthStr = '';
            break;
        case 4:
            monthStr = '';
            break;
        case 5:
            monthStr = '';
            break;
        case 6:
            monthStr = '';
            break;
        case 7:
            monthStr = '';
            break;
        case 8:
            monthStr = '';
            break;
        case 9:
            monthStr = '';
            break;
        case 10:
            monthStr = '';
            break;
        case 11:
            monthStr = '';
            break;
        default:
            break;
    }
    return `${dayDate} ${monthStr}`;
};
